	module.exports = {"data":{"post":{"html":"<p>On a day like today, nobody should care that I am home sick and miserable. Or that tomorrow I have to choose between the lesser of two evils, between going to work sick or staying home with Christopher and Oma (i.e. my mother-in-law).</p>\n<p>I need to get well by Saturday, since Christopher and I are taking the ICE train to Berlin! Mama has a one day break between meetings, and she has invited us to her luxury hotel suite. </p>\n<p>ZDF has just admitted that they have nothing new to report, but they continue to broadcast their special report anyway.</p>","fields":{"title":"Nobody Should Care","date":"20 Mar 2003","url":"https://papascott.de/2003-03-20-nobody-should-care/"}}},"pathContext":{"slug":"/2003-03-20-nobody-should-care/","prev":{"fields":{"title":"Mobbing: Alles Quatsch","date":"19 Mar 2003","slug":"/2003-03-19-mobbing-alles-quatsch/"}},"next":{"fields":{"title":"","date":"21 Mar 2003","slug":"/2003-03-21-cipro-ueber-alles/"}}}}

